<script>
  import { onMount } from 'svelte'
  // import { line, curve, curveCatmullRom } from "d3-shape";
  // import { scaleTime, scaleLinear } from "d3-scale";
  // import { axisBottom, axisLeft } from 'd3-axis';
  // import { timeParse, isoFormat } from "d3-time-format";
  // import { select } from "d3-selection";
  // import { extent, max, min } from "d3-array";
  // import { fly } from 'svelte/transition'
  let data = []
  import { distribution } from '../data/charts-data.js'
  import { scaleLinear, scaleBand } from 'd3-scale'
  import { select } from 'd3-selection'
  import { max } from 'd3-array'
  const d3 = { scaleLinear, scaleBand, max, select }
  const xData = [0, 20, 40, 60]

  const margin = { top: 0, right: 10, bottom: 40, left: 90 }
  let chartWidth = 100
  let width
  let height = 300

  $: width = chartWidth - margin.left - margin.right
  $: yScale = d3
    .scaleBand()
    .domain(data.map((d) => d.bucket))
    .range([margin.top, height - margin.bottom])
    .paddingInner(0.15)

  $: xScale = d3
    .scaleLinear()
    .domain([0, 70])
    .range([margin.left, width - margin.right])

  onMount(() => {
    data = distribution.reverse()
  })
</script>

<figure>
  <h3>Indeks-tellings vir munisipaliteite</h3>
  <div class="chart" bind:clientWidth={chartWidth}>
    <svg {width} {height}>
      <!-- X axis labels-->
      {#each xData as x}
        <text class="bar-x-axis" x={xScale(x)} y={height - 15}>{x}</text>
        <line
          class="x-grid"
          x1={xScale(x)}
          x2={xScale(x)}
          y1={margin.top - 5}
          y2={height - 30}
        />
      {/each}
      {#each data as d, i}
        <!-- Y axis labels-->
        <text
          class="bar-text"
          x={margin.left - 10}
          y={yScale(d.bucket) + yScale.bandwidth() * 0.75}
        >
          {d.bucket}
        </text>

        <!-- Background bars -->
        <rect
          class="bar-background"
          y={yScale(d.bucket)}
          height={yScale.bandwidth()}
          width={xScale(70) - margin.left}
          x={margin.left}
        />
        <rect
          class="bar"
          y={yScale(d.bucket)}
          height={yScale.bandwidth()}
          width={xScale(d.count) - margin.left}
          x={margin.left}
        />

        <text
          class="bar-label"
          x={xScale(d.count) + 7}
          y={yScale(d.bucket) + yScale.bandwidth() * 0.65}
        >
          {d.count}
        </text>

        <!-- in:fly={{ x: -200, duration: 1000, delay: i * 50 }} -->
      {/each}
    </svg>
  </div>
</figure>

<style>
  .bar-label {
    text-anchor: start;
    fill: #000;
  }
</style>
