<script>
  import { selected, provincesMeta } from './stores/current-data.js'
  import { onMount } from 'svelte'
  import { SimpleGauge } from './d3-simple-gauge.js'
  let gauges = []
  let topProvinces = []
  let bottomProvinces = []

  function getTopProvinces(selected) {
    return selected.provData
      .sort((a, b) => (a.total > b.total ? -1 : 1))
      .slice(0, 4)
  }
  function getBottomProvinces(selected) {
    return selected.provData
      .sort((a, b) => (a.total > b.total ? 1 : -1))
      .slice(0, 4)
  }

  $: topProvinces = getTopProvinces($selected)
  $: bottomProvinces = getBottomProvinces($selected)

  function changeGauges() {
    if (gauges.length > 0) {
      gauges[1].value = topProvinces[0].total
      gauges[2].value = topProvinces[1].total
      gauges[3].value = topProvinces[2].total
      gauges[4].value = topProvinces[3].total
      gauges[5].value = bottomProvinces[0].total
      gauges[6].value = bottomProvinces[1].total
      gauges[7].value = bottomProvinces[2].total
      gauges[8].value = bottomProvinces[3].total
    }
  }

  // import { select } from 'd3-selection'
  // const d3 = { select }

  $: if ($selected.provCode) {
    changeGauges()
  }

  function addGauge(target, index) {
    const svg = d3
      .select('.' + target)
      .append('svg')
      .attr('class', 'svg')
      .attr('width', 150)
      .attr('height', 110)

    gauges[index] = new SimpleGauge({
      el: svg.append('g'), // The element that hosts the gauge
      height: 100, // The height of the gauge
      interval: [0, 100], // The interval (min and max values) of the gauge (optional)
      sectionsCount: 10, // The number of sections in the gauge
      width: 150, // The width of the gauge
      animationDuration: 1000,
      needleColor: '#000',
      chartInset: 0,
      easeType: d3.easeBounce,
      barWidth: 40,
      needleRadius: 7,
      sectionsColors: [
        '#A50728',
        '#E14B39',
        '#F78D59',
        '#FDC47C',
        '#FEEFAA',
        '#EBF6A9',
        '#C1E484',
        '#84CA6F',
        '#44AA5F',
        '#07693B',
      ],
    })
    gauges[index].value = 0
  }

  onMount(() => {
    addGauge('top-1', 1)
    addGauge('top-2', 2)
    addGauge('top-3', 3)
    addGauge('top-4', 4)
    addGauge('bottom-1', 5)
    addGauge('bottom-2', 6)
    addGauge('bottom-3', 7)
    addGauge('bottom-4', 8)

    setTimeout(() => {
      changeGauges()
    }, 1000)
  })
</script>

<main>
  <div class="top-gauges">
    <!-- <div class="section-title"><h2>{$selected.provName}</h2></div> -->
    <div class="top-title title-best">Beste presteerders</div>
    <div class="top-gauges-wrap">
      {#each topProvinces as top, i}
        <div>
          <div class="gauge top-{i + 1}" />
          <div class="gauge-title top-name-{i + 1}">{top.name}</div>
        </div>
      {/each}
    </div>
    <div class="top-title title-worst">Slegste presteerders</div>
    <div class="top-gauges-wrap">
      {#each bottomProvinces as bottom, i}
        <div>
          <div class="gauge bottom-{i + 1}" />
          <div class="gauge-title bottom-name-{i + 1}">{bottom.name}</div>
        </div>
      {/each}
    </div>
  </div>
</main>

