<script>
  function goMunicipal() {}
  import { page } from './stores/page-state.js'
  import { selected } from './stores/current-data'
  import { municipalData } from './stores/municipal-store.js'
  import { provincesMeta } from './stores/current-data.js'
  let selectedProvince = "Kies 'n provinsie"
  let selectedProvinceCode = null
  let municipalities = []
  let selectedMunicipality = null
  import { elections, keyPoints } from './stores/key-points.js'
  import { indiMeta, indiData } from './stores/indicators.js'

  // update to default if selected
  // selectedProvince = $selected.provName
  if ($selected.provName) {
    selectedProvince = $selected.provName
    selectedMunicipality = $selected.muniName
  }

  $: if (selectedProvince !== "Kies 'n provinsie") {
    updateSelection(selectedProvince)
  }

  function updateSelection(sp) {
    selectedProvinceCode = $provincesMeta.filter((f) => f.name === sp)[0].inCode
    municipalities = $municipalData.filter(
      (f) => f.province === selectedProvinceCode
    )
  }

  function selectMunicipality(selectedMunicipality) {
    if (selectedMunicipality) {
      $page.municipal = true
      $page.home = false
      $page.province = false

      let mcode = municipalities.filter(
        (f) => f.name === selectedMunicipality
      )[0].code

      selected.update((val) => {
        val.provCode = selectedProvinceCode
        val.provName = selectedProvince
        val.muniCode = mcode
        val.muniData = $municipalData.filter((f) => f.code === mcode)[0]
        val.muniName = $municipalData.filter((f) => f.code === mcode)[0].name
        val.elections = $elections.filter((f) => f.Code === mcode)
        val.keyPoints = $keyPoints.filter((f) => f.code === mcode)[0]
        val.indData = $indiData.filter((f) => f.code === mcode)[0]
        val.indMeta = $indiMeta

        return val
      })
    }
  }

  $: selectMunicipality(selectedMunicipality)

  function goHome() {
    page.update((val) => {
      val.home = true
      val.province = false
      val.municipal = false
      municipalities = []
      selectedProvince = "Kies 'n provinsie"
      return val
    })
  }
</script>

<div class="selector">
  <select id="prov" bind:value={selectedProvince}>
    <option>Kies 'n provinsie</option>
    {#each $provincesMeta as p}
      <option value={p.name}>{p.afrName}</option>
    {/each}
  </select>

  {#if municipalities.length > 0}
    <select id="muni" bind:value={selectedMunicipality}>
      {#each municipalities as m}
        <option>{m.name}</option>
      {/each}
    </select>
  {/if}

  <!-- {#if selectedProvince !== 'Select a province'}
    <button class="reset" on:click={goHome}>Reset</button>
  {/if} -->
</div>

<style>
  select {
    padding: 10px;
    margin-top: 0px;
    border: solid 1px lightgray;
    background: #eee;
  }
  .selector {
    width: 100%;
    min-width: 150px;
    /* margin-top: 20px; */
    padding-top: 2px;
    margin-top: 0px;
  }
  option {
    min-width: 150px;
  }
  button {
    padding: 10px 10px;
    border: solid 1px rgb(187, 187, 187);
    border-radius: 0px;
    cursor: pointer;
    margin-top: 10px;
    background: gray;
    color: #fff;
  }

  @media only screen and (max-width: 600px) {
    .selector {
      padding: 20px;
      text-align: center;
      max-width: 90%;
    }
    .selector * {
      margin-left: auto;
      margin-right: auto;
      display: block;
    }
    .selector select {
      margin-top: 10px;
    }
  }
</style>
