import { readable } from 'svelte/store'
import { munis } from '../../data/municipalities.js'

munis.forEach((m) => {
  // m.name = m['Municipality name']
  // m.code = m['Municipal MDB codes']
  // m.total = m.Total
  if (m.province === 'GT') {
    m.province = 'GP'
  }
  if (m.province === 'LIM') {
    m.province = 'LP'
  }
})

const municipalData = readable(munis)

export { municipalData }
