<script>
  import { selected, provincesMeta } from './stores/current-data.js'
  import { municipalData } from './stores/municipal-store.js'
  import { page } from './stores/page-state.js'
  export let buttonColor = '#eee'

  // export let showProvincePage

  function selectProvince(code) {
    selected.update((val) => {
      val.provCode = code
      val.provName = $provincesMeta.filter((f) => f.inCode === code)[0].name
      val.provData = $municipalData
        .filter((f) => f.province === code)
        .sort((a, b) => {
          a.total > b.total ? -1 : 1
        })

      return val
    })
    page.update((val) => {
      val.province = true
      val.home = false
      return val
    })
  }
</script>

<div class="province-list">
  <div class="province-list-wrap">
    {#each $provincesMeta as province}
      <div
        class="province-button {$selected.provCode === province.inCode
          ? 'selected'
          : ''}"
        on:click={selectProvince(province.inCode)}
      >
        {province.afrName}
      </div>
    {/each}
  </div>
</div>

