<script>
  import { onMount } from 'svelte'
  import { selected } from './stores/current-data.js'

  const formatThousands = new Intl.NumberFormat()

  function formatIndicatorNumbers(meta) {
    let number = $selected.indData[meta.indicator]
    let value
    if (number === 'No data') {
      value = 'Geen Data'
    } else if (meta.unit === '%') {
      value = number
    } else {
      let num = $selected.indData[meta.indicator].toString().replaceAll(',', '')

      num = formatThousands.format(+num)
      if (meta.unit === 'R') {
        value = 'R' + num
      } else {
        value = num
      }
    }

    return value
  }

  let m = { x: 0, y: 0 }
  let display = 'none'
  let tooltiptext = 'tooltip'

  function handleMouseover(id) {
    let t = $selected.indMeta.filter((d, i) => i === id)
    tooltiptext = t[0]['explainer_for_hover_over']
    display = 'block'
  }

  function handleMousemove(e) {
    m.x = e.pageX + 10
    m.y = e.pageY
  }
  function handleMouseout() {
    m.x = -1000
  }
</script>

<div class="indicators">
  <div class="hovers" style="top: {m.y}px; left: {m.x}px; display: {display}">
    {tooltiptext}
  </div>

  <!-- svelte-ignore a11y-mouse-events-have-key-events -->
  <div class="indicator-wrap">
    {#each $selected.indMeta as m, i}
      <div
        class="indicator"
        on:mouseover={() => {
          handleMouseover(i)
        }}
        on:mousemove={handleMousemove}
        on:mouseout={handleMouseout}
      >
        <div class="indicator-image">
          <img src="./assets/outofordericons/SVG/{m.icon}" alt="icon" />
        </div>
        <div class="indicator-detail">
          <div class="indicator-text">{m.label}</div>
          <div
            class="indicator-number {formatIndicatorNumbers(m) === 'Geen Data'
              ? 'no-data'
              : ''}"
          >
            {formatIndicatorNumbers(m)}
          </div>
        </div>
      </div>
    {/each}
  </div>
</div>

<style>
  .indicator-wrap {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
  }
  .indicator-image img {
    width: 50px;
  }
  .indicator {
    text-align: center;
    cursor: pointer;
  }
  @media only screen and (max-width: 600px) {
    .indicator-wrap {
      display: grid;
      grid-template-columns: 1fr;
    }
  }
</style>
