<script>
  import { elections, keyPoints } from './stores/key-points.js'
  import { indiMeta, indiData } from './stores/indicators.js'
  import { selected, provincesMeta } from './stores/current-data.js'
  import { page } from './stores/page-state.js'
  import { municipalData } from './stores/municipal-store.js'
  import { getMunicipalityName } from './functions.js'
  import { provs } from '../data/provinces.js'
  export let provList = []
  import chroma from 'chroma-js'
  export let scale = chroma.bezier(['red', 'yellow', 'green'])
  import { onMount } from 'svelte'
  import L from 'leaflet'
  import { municipalMap } from '../data/municipal-map.js'
  import { gauteng } from '../data/provinces/gp.js'
  import { easterncape } from '../data/provinces/ec.js'
  import { northerncape } from '../data/provinces/nc.js'
  import { westerncape } from '../data/provinces/wc.js'
  import { northwest } from '../data/provinces/nw.js'
  import { freestate } from '../data/provinces/fs.js'
  import { kwazulunatal } from '../data/provinces/kzn.js'
  import { mpumalanga } from '../data/provinces/mp.js'
  import { limpopo } from '../data/provinces/lp.js'
  let tooltip
  let mapWidth
  let mapHeight
  $: provList = provs

  onMount(() => {
    setTimeout(() => {
      addMap()
      tooltip = document.querySelector('.tooltip')
      mapWidth = document.getElementById('map').offsetWidth
      mapHeight = document.getElementById('map').offsetHeight
    }, 1000)
  })

  function onEachFeature(feature, layer) {
    layer.on({
      mouseover: hover,
      mousemove: move,
      mouseout: out,
      click: click,
    })
  }

  function provinceStyle(feature) {
    return {
      color: '#fff',
      weight: 1,
      fillColor: 'none',
    }
  }

  function mapStyle(feature) {
    let code = feature.properties.ADM3_ID
    let cm = []
    $municipalData.forEach((e) => {
      if (e.code === code) {
        cm = e
      }
    })

    let fillColor = '#eee'

    fillColor = scale(+cm['total'] / 100)

    let borderColor = 'gray'
    return {
      color: borderColor,
      fillColor: fillColor,
      weight: 0.5,
      opacity: 1,
      fillOpacity: 1,
      apiUrl: '',
      //   className: feature.properties.ADM0_A3 + ' country',
    }
  }

  function hover(feature) {
    let pcode = feature.target.feature.properties.ADM1_ID
    let mcode = feature.target.feature.properties.ADM3_ID
    let name = getMunicipalityName(mcode, $municipalData)

    let index = $municipalData.filter((f) => f.code === mcode)[0].total

    let provName = provList.filter((p) => p.code === pcode)[0].name
    let tt = `<div class="tt-wrap">
            <div class="tt-title">${name}</div>
            <div class="tt-province">${provName}</div>
            <div class="tt-body">
              <div class="tt-label">Municipality Performance</div>
              <div class="tt-bar-wrap">
                <div class="tt-bar" style="width: ${index}%;">${index}</div>
                </div>
            </div>
        </div>`
    tooltip.innerHTML = tt
  }
  function move(feature) {
    let xPos = feature.containerPoint.x
    let yPos = feature.containerPoint.y

    if (mapWidth - xPos > mapWidth / 2) {
      tooltip.style.left = feature.containerPoint.x + 10 + 'px'
    } else {
      tooltip.style.left = feature.containerPoint.x - 180 + 'px'
    }
    if (yPos < mapHeight - 100) {
      tooltip.style.top = feature.containerPoint.y + 0 + 'px'
    } else {
      tooltip.style.top = feature.containerPoint.y - 100 + 'px'
    }
  }
  function out() {
    tooltip.style.left = '-1000px'
  }
  function click(feature) {
    let code = feature.target.feature.properties.ADM3_ID
    showMunicipality(code)
  }

  let map
  let homeMap
  let pMap
  let gpMap
  let ecMap
  let ncMap
  let wcMap
  let nwMap
  let mpMap
  let kznMap
  let fsMap
  let lpMap
  let zoomed = false
  let mapProvince = null

  function addMap() {
    map = L.map('map', {
      zoomControl: false,
      scrollWheelZoom: false,
      dragging: false,
      zoomSnap: 0.1,
    })

    homeMap = L.geoJSON(municipalMap, {
      onEachFeature: onEachFeature,
      style: mapStyle,
    }).addTo(map)

    map.fitBounds(homeMap.getBounds())

    gpMap = L.geoJSON(gauteng, {
      style: provinceStyle,
    }).addTo(map)

    ecMap = L.geoJSON(easterncape, {
      style: provinceStyle,
    }).addTo(map)

    ncMap = L.geoJSON(northerncape, {
      style: provinceStyle,
    }).addTo(map)

    wcMap = L.geoJSON(westerncape, {
      style: provinceStyle,
    }).addTo(map)

    fsMap = L.geoJSON(freestate, {
      style: provinceStyle,
    }).addTo(map)

    kznMap = L.geoJSON(kwazulunatal, {
      style: provinceStyle,
    }).addTo(map)

    lpMap = L.geoJSON(limpopo, {
      style: provinceStyle,
    }).addTo(map)

    mpMap = L.geoJSON(mpumalanga, {
      style: provinceStyle,
    }).addTo(map)

    nwMap = L.geoJSON(northwest, {
      style: provinceStyle,
    }).addTo(map)
  }

  function zoomProvince(event) {
    let prov = event.target.dataset.prov
    if (prov === 'GP') {
      map.fitBounds(gpMap.getBounds())
      zoomed = true
      mapProvince = prov
    }
    if (prov === 'LP') {
      map.fitBounds(lpMap.getBounds())
      zoomed = true
      mapProvince = prov
    }
    if (prov === 'MP') {
      map.fitBounds(mpMap.getBounds())
      zoomed = true
      mapProvince = prov
    }
    if (prov === 'FS') {
      map.fitBounds(fsMap.getBounds())
      zoomed = true
      mapProvince = prov
    }
    if (prov === 'NC') {
      map.fitBounds(ncMap.getBounds())
      zoomed = true
      mapProvince = prov
    }
    if (prov === 'WC') {
      map.fitBounds(wcMap.getBounds())
      zoomed = true
      mapProvince = prov
    }
    if (prov === 'EC') {
      map.fitBounds(ecMap.getBounds())
      zoomed = true
      mapProvince = prov
    }
    if (prov === 'KZN') {
      map.fitBounds(kznMap.getBounds())
      zoomed = true
      mapProvince = prov
    }
    if (prov === 'NW') {
      map.fitBounds(nwMap.getBounds())
      zoomed = true
      mapProvince = prov
    }
    if (prov === 'RS') {
      map.fitBounds(homeMap.getBounds())
      zoomed = false
      mapProvince = null
    }
  }

  function showMunicipality(code) {
    $page.scrollMunicipality = true

    $page.home = false
    $page.province = false
    $page.municipal = true

    selected.update((val) => {
      val.provCode = $municipalData.filter((f) => f.code === code)[0].province

      val.muniCode = code
      val.muniData = $municipalData.filter((f) => f.code === code)[0]
      val.muniName = $municipalData.filter((f) => f.code === code)[0].name
      val.elections = $elections.filter((f) => f.Code === code)
      val.keyPoints = $keyPoints.filter((f) => f.code === code)[0]
      val.indData = $indiData.filter((f) => f.code === code)[0]
      val.indMeta = $indiMeta

      val.provName = $provincesMeta.filter(
        (f) => f.inCode === val.provCode
      )[0].name

      return val
    })
  }
</script>

<div class="map-wrap">
  <div class="legend">
    <div class="legend-title">Prestasie van munisipaliteite</div>
    <div class="legend-label">Indekswaarde uit 100</div>
    <div class="legend-wrap">
      <div class="legend-block" style="background: {scale(0.1)};">10</div>
      <div class="legend-block" style="background: {scale(0.2)};">20</div>
      <div class="legend-block" style="background: {scale(0.3)};">30</div>
      <div class="legend-block" style="background: {scale(0.4)};">40</div>
      <div class="legend-block" style="background: {scale(0.5)};">50</div>
      <div class="legend-block" style="background: {scale(0.6)};">60</div>
      <div class="legend-block" style="background: {scale(0.7)};">70</div>
      <div class="legend-block" style="background: {scale(0.8)};">80</div>
      <div class="legend-block" style="background: {scale(0.9)};">90</div>
      <div class="legend-block" style="background: {scale(1)};">100</div>
    </div>
    <div class="legend-label legend-label-two">Kies ’n provinsie</div>
    <div class="province-link-wrap">
      <div
        class="province-link {mapProvince === 'EC' ? 'province-selected' : ''}"
        on:click={zoomProvince}
        data-prov="EC"
      >
        OK
      </div>
      <div
        class="province-link {mapProvince === 'FS' ? 'province-selected' : ''}"
        on:click={zoomProvince}
        data-prov="FS"
      >
        VS
      </div>
      <div
        class="province-link {mapProvince === 'GP' ? 'province-selected' : ''}"
        on:click={zoomProvince}
        data-prov="GP"
      >
        GP
      </div>
      <div
        class="province-link {mapProvince === 'KZN' ? 'province-selected' : ''}"
        on:click={zoomProvince}
        data-prov="KZN"
      >
        KZN
      </div>
      <div
        class="province-link {mapProvince === 'LP' ? 'province-selected' : ''}"
        on:click={zoomProvince}
        data-prov="LP"
      >
        LP
      </div>
      <div
        class="province-link {mapProvince === 'MP' ? 'province-selected' : ''}"
        on:click={zoomProvince}
        data-prov="MP"
      >
        MP
      </div>
      <div
        class="province-link {mapProvince === 'NW' ? 'province-selected' : ''}"
        on:click={zoomProvince}
        data-prov="NW"
      >
        NW
      </div>
      <div
        class="province-link {mapProvince === 'NC' ? 'province-selected' : ''}"
        on:click={zoomProvince}
        data-prov="NC"
      >
        NK
      </div>
      <div
        class="province-link {mapProvince === 'WC' ? 'province-selected' : ''}"
        on:click={zoomProvince}
        data-prov="WC"
      >
        WK
      </div>
      <div
        class="province-link province-reset {!zoomed ? 'no-reset' : ''}"
        on:click={zoomProvince}
        data-prov="RS"
      >
        RESET
      </div>
    </div>
  </div>
  <div id="map" class="map">
    <div class="tooltip">Tooltip</div>
  </div>
</div>

<style>
  #map {
    margin-top: 30px;
    /* position: relative; */
    z-index: 30000;
    width: 100%;
    min-height: 500px;
    border: solid 1px lightgray;
    background: #eee;
  }
  .map-wrap {
    position: relative;
  }
  .legend {
    pointer-events: none;
    position: absolute;
    z-index: 100000;
    top: 20px;
    left: 20px;
    /* text-transform: uppercase; */
    text-align: left;
    font-size: 0.7rem;
  }
  .legend-label {
    margin-bottom: 3px;
  }
  .legend-label-two {
    margin-top: 10px;
    margin-bottom: 0px;
  }
  .legend-title {
    font-size: 1.2rem;
    font-family: var(--bold-face);
  }
  .legend-wrap {
    margin-top: 0px;
    display: grid;
    grid-template-columns: repeat(10, 20px);
    grid-gap: 1px;
    margin-bottom: 5px;
  }
  .legend-block {
    width: 100%;
    background: rgb(247, 247, 247);
    font-size: 0.5rem;
    text-align: center;
    color: #fff;
    padding-top: 5px;
    padding-bottom: 5px;
    border: solid 1px #fff;
  }
  .province-link-wrap {
    max-width: 40px;
    display: grid;
    grid-template-columns: 1fr;
    padding-right: 10px;
    margin-top: 10px;
    grid-gap: 5px;
  }
  .province-link {
    pointer-events: all;
    padding: 2px 5px 1px 5px;
    background: #fff;
    border: solid 1px lightgray;
    color: #000;
    cursor: pointer;
    border-radius: 5px;
    text-align: center;
    font-size: 0.6rem;
    font-family: var(--bold-face);
  }
  .province-reset {
    background: red;
    border: none;
    color: #fff;
  }
  .no-reset {
    display: none;
  }
  .province-selected {
    background: #000;
    color: #fff;
    border: solid 1px #000;
  }
</style>
