<script>
  import { onMount } from 'svelte'
  // Import page components
  import Homepage from './Homepage.svelte'
  import HowToUse from './HowToUse.svelte'

  // import data
  import { page } from './stores/page-state.js'
  import { selected } from './stores/current-data'

  $: if ($page.home && $page.scrollHome) {
    if (document.querySelector('.logo')) {
      window.scrollTo(0, 0)
      $page.scrollProvince = true
      $page.scrollMunicipality = true
    }
  }
  $: if ($page.province && $page.scrollProvince) {
    let h = document.querySelector('.logo').offsetHeight
    window.scrollTo(0, h)
    $page.scrollProvince = false
  }
  $: if ($page.municipal && $page.scrollMunicipality) {
    let h = document.querySelector('.logo').offsetHeight

    setTimeout(() => {
      window.scrollTo(0, h)
    }, 300)
  }

  // resets
  $: if ($page.home) {
    $selected.provCode = null
  }
