<script>
  import { page } from './stores/page-state'
  function closeHowTo() {
    $page.howTo = false
  }
</script>

<div class="how-to">
  <div class="how-to-inner">
    <div class="close-bar-top">
      <img src="./assets/close.svg" alt="close button" on:click={closeHowTo} />
    </div>
    <section>
      <h2>Só gebruik jy die indeks</h2>
      <p>
        Die Buite Werking-indeks plaas meer as 200 plaaslike munisipaliteite en
        agt metro’s op ’n skaal van 0 tot 100. Nul verteenwoordig totale verval
        en 100 dui op perfekte funksionering. Wanneer jy op ’n munisipaliteit
        klik, sal jy in besonderhede kan sien hoe die indeks op die meter
        vertoon. Rooi aan die linkerkant is swak, terwyl groen regs dui op goed.
        Onder elke meter is daar verskeie hefboompies.
      </p>
      <p class="how-to-image">
        <img src="./assets/ooo-afrikaans-screenshot.png" alt="levers1" />
      </p>
      <p>
        Ons het hierdie beleidshefbome geskep vir interaksie met gebruikers
        sodat hulle kan verstaan hoe ’n munisipaliteit se welstand kan verander.
        Elke hefboom word op die middelpunt gestel, wat die stand van daardie
        hefboom se onderliggende indekswaardes verteenwoordig. Deur ’n hefboom
        na links te skuif, sal jy sien dat die impak van daardie situasie
        versleg. Byvoorbeeld, indien jy die Opgeleide Personeel-hefboom na links
        skuif, dui dit aan daar is minder opgeleide amptenare in die
        munisipaliteit, en sal die indeks nogal drasties na die rooi sone
        beweeg.
      </p>
      <!-- <p class="how-to-image">
        <img src="./assets/levers2.png" alt="levers2" />
      </p> -->
      <p>
        Daar is perke op hoe ver elke beleidshefboom kan beweeg en dit word
        bepaal deur die waarde van die data wat dit onderlê. Sommige van hulle
        sal ’n drastiese impak hê, terwyl ander heel gematig sal wees. Jy kan in
        enige stadium op die “herstel”-knoppie druk om die oorspronklike waardes
        te herstel.
      </p>
      <h2>VRYWARING</h2>
      <p>
        Die is belangrik om te onthou dat die Buitewerking-projek datagedrewe
        joernalistiek is wat daarop gemik is om deur die gebruik van ’n reeks
        databronne aan ons lesers ’n blik te gee op die stand van munisipale
        regering in die land. Dit doel is om aan mense nuttige inligting te gee
        wat hulle kan help om ingeligte besluite te neem, byvoorbeeld wanneer
        hulle moet besluit vir wie om te stem of wil verstaan watter belangrike
        veranderinge kan kom in hul munisipaliteit. Dit is nie ’n amptelike
        ekonomiese of scenario-modelleringstoepassing nie en moet ook nie as
        sodanig beskou word nie. Dit is gegrond op data van die gesaghebbendste
        bronne beskikbaar in Suid-Afrika soos die nasional tesourie se data wat
        gebruik word om toelaes aan munisipaliteite te bepaal, die
        ouditeur-generaal se verslae en Statistieke Suid-Afrika. Maar dit is ook
        belangrik om te onthou dat dié data ver van perfek is, sommige daarvan
        is taamlik verouderd, by sommiges is daar data wat ontbreek en van die
        data kan selfs verkeerd wees, want dit is soos dit ingesamel en aan die
        staatsdepartemente verskaf is. Die span datajoernaliste het dit so goed
        as moontlik probeer hanteer maar daar is baie wat ons dalk nie weet nie.
      </p>
    </section>
    <div class="close-bar-bottom">
      <button on:click={closeHowTo}>Maak toe</button>
    </div>
  </div>
</div>

