<script>
  import { selected, provincesMeta } from './stores/current-data.js'
  import { municipalData } from './stores/municipal-store.js'
  import { provincialData } from './stores/provinces-store.js'
  import { page } from './stores/page-state.js'
  import { elections, keyPoints } from './stores/key-points.js'
  import { indiMeta, indiData } from './stores/indicators.js'

  import ProvinceButtons from './ProvinceButtons.svelte'
  import TopGauges from './TopGauges.svelte'

  function selectMunicipality(code) {
    $page.province = false
    $page.municipal = true

    selected.update((val) => {
      val.muniCode = code
      val.muniData = $municipalData.filter((f) => f.code === code)[0]
      val.muniName = $municipalData.filter((f) => f.code === code)[0].name
      val.elections = $elections.filter((f) => f.Code === code)
      val.keyPoints = $keyPoints.filter((f) => f.code === code)[0]
      val.indData = $indiData.filter((f) => f.code === code)[0]
      val.indMeta = $indiMeta

      return val
    })
  }
  $: $selected.provData = $selected.provData.sort((a, b) =>
    a.total < b.total ? 1 : -1
  )
</script>

<div class="province-page">
  <section>
    <div class="bottom-provinces">
      <ProvinceButtons />
    </div>
  </section>
  <section>
    <TopGauges />

    <!-- <div class="section-small-head">{$selected.provName}</div> -->
    <div class="section-title"><h1>Munisipaliteite</h1></div>
    <div>
      Munisipaliteite word gegradeer uit 'n telling van 100. Die telling
      hieronder verteenwoordig die indekswaarde vir elke munisipaliteit
    </div>
    <div class="section-note">
      Klik op 'n munisipaliteit hieronder vir meer inligting
    </div>
    <div class="municipal-listing">
      {#each $selected.provData as municipality (municipality.code)}
        <div
          class="municipality-wrap"
          on:click={selectMunicipality(municipality.code)}
        >
          <div class="municipal-name">{municipality.name}</div>
          <div class="municipal-bar-wrap">
            <div class="municipal-bar" style="width: {municipality.total}%;">
              {municipality.total}
            </div>
          </div>
        </div>
      {/each}
    </div>
  </section>
</div>

