<script>
  import { onMount } from 'svelte'
  // import { line, curve, curveCatmullRom } from "d3-shape";
  // import { scaleTime, scaleLinear } from "d3-scale";
  // import { axisBottom, axisLeft } from 'd3-axis';
  // import { timeParse, isoFormat } from "d3-time-format";
  // import { select } from "d3-selection";
  // import { extent, max, min } from "d3-array";
  // import { fly } from 'svelte/transition'
  let data = []
  import { repairs } from '../data/charts-data.js'
  import { scaleLinear, scaleBand } from 'd3-scale'
  import { select } from 'd3-selection'
  import { max } from 'd3-array'
  const d3 = { scaleLinear, scaleBand, max, select }
  const xData = [0, 20, 40, 60, 80, 100]

  const margin = { top: 0, right: 10, bottom: 40, left: 120 }
  let chartWidth = 100
  let width
  let height = 180

  $: width = chartWidth - margin.left - margin.right
  $: yScale = d3
    .scaleBand()
    .domain(data.map((d) => d.municipality))
    .range([margin.top, height - margin.bottom])
    .paddingInner(0.15)

  $: xScale = d3
    .scaleLinear()
    .domain([0, 100])
    .range([margin.left, width - margin.right])

  onMount(() => {
    data = repairs
  })
</script>

<figure>
  <h3>
    Persentasie van die begroting wat aan herstelwerk en onderhoud bestee word
  </h3>
  <div class="chart" bind:clientWidth={chartWidth}>
    <svg {width} {height}>
      <!-- X axis labels-->
      {#each xData as x}
        <text class="bar-x-axis" x={xScale(x)} y={height - 15}>{x}</text>
        <line
          class="x-grid"
          x1={xScale(x)}
          x2={xScale(x)}
          y1={margin.top - 5}
          y2={height - 30}
        />
      {/each}
      {#each data as d, i}
        <!-- Y axis labels-->
        <text
          class="bar-text"
          x={margin.left - 10}
          y={yScale(d.municipality) + yScale.bandwidth() * 0.75}
        >
          {d.municipality}
        </text>

        <!-- Background bars -->
        <rect
          class="bar-background"
          y={yScale(d.municipality)}
          height={yScale.bandwidth()}
          width={xScale(100) - margin.left}
          x={margin.left}
        />
        <rect
          class="bar"
          y={yScale(d.municipality)}
          height={yScale.bandwidth()}
          width={xScale(d.percent_spent_on_repairs) - margin.left}
          x={margin.left}
        />

        <text
          class="bar-label"
          x={xScale(d.percent_spent_on_repairs) + 5}
          y={yScale(d.municipality) + yScale.bandwidth() * 0.65}
        >
          {d.percent_spent_on_repairs}%
        </text>

        <!-- in:fly={{ x: -200, duration: 1000, delay: i * 50 }} -->
      {/each}
    </svg>
  </div>
</figure>

<style>
  figure {
    padding: 0px;
    margin: 0px;
  }
  .bar-label {
    text-anchor: start;
    fill: #000;
  }
</style>
