<script>
  import { map_points } from '../data/map-points.js'
  let addMapCount = 0
  import { onMount } from 'svelte'
  import L from 'leaflet'
  import MunicipalGauge from './MunicipalGauge.svelte'
  import MunicipalIndicators from './MunicipalIndicators.svelte'
  import MunicipalSelector from './MunicipalSelector.svelte'
  import { page } from './stores/page-state.js'

  import { selected } from './stores/current-data.js'
  // import { keyPoints } from './stores/key-points.js'

  import { easterncape } from '../data/provinces/ec.js'
  import { northerncape } from '../data/provinces/nc.js'
  import { westerncape } from '../data/provinces/wc.js'
  import { northwest } from '../data/provinces/nw.js'
  import { freestate } from '../data/provinces/fs.js'
  import { kwazulunatal } from '../data/provinces/kzn.js'
  import { mpumalanga } from '../data/provinces/mp.js'
  import { limpopo } from '../data/provinces/lp.js'
  import { gauteng } from '../data/provinces/gp.js'

  import { provinceMap } from '../data/province-map.js'
  import { municipalMap } from '../data/municipal-map.js'

  $: $selected.elections = $selected.elections
    .sort((a, b) => (a.PercentOfVote > b.PercentOfVote ? -1 : 1))
    .filter((d, i) => i < 4)

  function mapStyle(feature) {
    let fillColor = 'none'

    let borderColor = 'lightgray'
    return {
      color: borderColor,
      fillColor: fillColor,
      weight: 0.5,
      opacity: 1,
      fillOpacity: 0,
      apiUrl: '',
      className: feature.properties.ADM3_ID + '  municipality',
    }
  }

  function provinceStyle() {
    let fillColor = 'none'

    let borderColor = 'gray'
    return {
      color: borderColor,
      fillColor: fillColor,
      weight: 0.5,
      opacity: 1,
      fillOpacity: 1,
    }
  }

  function currentProvinceStyle() {
    let fillColor = 'none'

    let borderColor = 'none'
    return {
      color: borderColor,
      fillColor: fillColor,
      weight: 2,
      opacity: 1,
      fillOpacity: 1,
    }
  }

  let map
  let ecMap
  let gpMap
  let kznMap
  let wcMap
  let ncMap
  let mpMap
  let lpMap
  let fsMap
  let nwMap

  function addMapContainer() {
    map = L.map('key-map', {
      zoomControl: false,
      scrollWheelZoom: false,
      dragging: false,
      zoomSnap: 0.1,
    })
  }

  function updateMapPosition() {
    if ($selected.muniData.province === 'EC') {
      map.fitBounds(ecMap.getBounds())
    }
    if ($selected.muniData.province === 'GT') {
      map.fitBounds(gpMap.getBounds())
    }
    if ($selected.muniData.province === 'GP') {
      map.fitBounds(gpMap.getBounds())
    }
    if ($selected.muniData.province === 'NC') {
      map.fitBounds(ncMap.getBounds())
    }
    if ($selected.muniData.province === 'WC') {
      map.fitBounds(wcMap.getBounds())
    }
    if ($selected.muniData.province === 'LIM') {
      map.fitBounds(lpMap.getBounds())
    }
    if ($selected.muniData.province === 'LP') {
      map.fitBounds(lpMap.getBounds())
    }
    if ($selected.muniData.province === 'FS') {
      map.fitBounds(fsMap.getBounds())
    }
    if ($selected.muniData.province === 'KZN') {
      map.fitBounds(kznMap.getBounds())
    }
    if ($selected.muniData.province === 'MP') {
      map.fitBounds(mpMap.getBounds())
    }
    if ($selected.muniData.province === 'NW') {
      map.fitBounds(nwMap.getBounds())
    }

    if ($selected.muniCode === 'GP') {
      $selected.muniCode = 'GT'
    }
    if ($selected.muniCode === 'LP') {
      $selected.muniCode = 'LIM'
    }
    let munis = document.querySelectorAll('.municipality')
    munis.forEach((e) => (e.style.fill = '#fff'))
    munis.forEach((e) => (e.style.fillOpacity = 0))
    document.querySelector('.' + $selected.muniCode).style.fill = '#ff4f4f'

    document.querySelector('.' + $selected.muniCode).style.fillOpacity = 1

    manageLabels().then(() => {
      if ($selected.provCode === 'GP') reAddLabels('.GP-label')
      if ($selected.provCode === 'MP') reAddLabels('.MP-label')
      if ($selected.provCode === 'LP') reAddLabels('.LP-label')
      if ($selected.provCode === 'FS') reAddLabels('.FS-label')
      if ($selected.provCode === 'KZN') reAddLabels('.KZN-label')
      if ($selected.provCode === 'WC') reAddLabels('.WC-label')
      if ($selected.provCode === 'EC') reAddLabels('.EC-label')
      if ($selected.provCode === 'NW') reAddLabels('.NW-label')
      if ($selected.provCode === 'NC') reAddLabels('.NC-label')
    })
  }

  function addMap() {
    let muniMap = L.geoJSON(municipalMap, {
      style: mapStyle,
    }).addTo(map)

    let provMap = L.geoJSON(provinceMap, {
      style: provinceStyle,
    }).addTo(map)

    ecMap = L.geoJSON(easterncape, {
      style: currentProvinceStyle,
    }).addTo(map)
    ncMap = L.geoJSON(northerncape, {
      style: currentProvinceStyle,
    }).addTo(map)
    wcMap = L.geoJSON(westerncape, {
      style: currentProvinceStyle,
    }).addTo(map)
    gpMap = L.geoJSON(gauteng, {
      style: currentProvinceStyle,
    }).addTo(map)
    nwMap = L.geoJSON(northwest, {
      style: currentProvinceStyle,
    }).addTo(map)
    fsMap = L.geoJSON(freestate, {
      style: currentProvinceStyle,
    }).addTo(map)
    kznMap = L.geoJSON(kwazulunatal, {
      style: currentProvinceStyle,
    }).addTo(map)
    mpMap = L.geoJSON(mpumalanga, {
      style: currentProvinceStyle,
    }).addTo(map)
    lpMap = L.geoJSON(limpopo, {
      style: currentProvinceStyle,
    }).addTo(map)

    updateMapPosition()

    // ADD MAP POINTS

    var pointLayer = L.geoJSON(null, {
      pointToLayer: function (feature, latlng) {
        let label = String(feature.properties.name) // Must convert to string, .bindTooltip can't use straight 'feature.properties.attribute'
        return new L.CircleMarker(latlng, {
          radius: 2,
          className: 'pointer ' + feature.properties.class,
        })
          .bindTooltip(label, {
            permanent: true,
            direction: 'center',
            className: 'points ' + feature.properties.class,
          })
          .openTooltip()
      },
    })
    pointLayer.addData(map_points)
    map.addLayer(pointLayer)

    // ADD MAP POINTS

    // hide labels
    manageLabels().then(() => {
      if ($selected.provCode === 'GP') reAddLabels('.GP-label')
      if ($selected.provCode === 'MP') reAddLabels('.MP-label')
      if ($selected.provCode === 'LP') reAddLabels('.LP-label')
      if ($selected.provCode === 'FS') reAddLabels('.FS-label')
      if ($selected.provCode === 'KZN') reAddLabels('.KZN-label')
      if ($selected.provCode === 'WC') reAddLabels('.WC-label')
      if ($selected.provCode === 'EC') reAddLabels('.EC-label')
      if ($selected.provCode === 'NW') reAddLabels('.NW-label')
      if ($selected.provCode === 'NC') reAddLabels('.NC-label')
    })

    addMapCount += 1
  }

  function reAddLabels(css) {
    let labels = document.querySelectorAll(css)
    labels.forEach((l) => {
      l.style.opacity = 1
    })
  }

  async function manageLabels() {
    await document.querySelectorAll('.points').forEach((l) => {
      l.style.opacity = 0
    })

    await document.querySelectorAll('.pointer').forEach((l) => {
      l.style.opacity = 0
    })
  }

  function updateMap() {
    if (addMapCount > 0) {
      updateMapPosition()
    }
  }

  $: if ($selected.muniCode) {
    updateMap()
  }

  // function goHome() {
  //   page.update((val) => {
  //     val.home = true
  //     val.province = false
  //     val.municipal = false
  //     return val
  //   })
  // }

  onMount(() => {
    addMapContainer()
    addMap()
  })
</script>

<div class="municipal-page">
  <div class="municipal-selector"><MunicipalSelector /></div>
  <section>
    <MunicipalGauge />

    <div class="section-title"><h1>Verkiesingsuitslae 2016</h1></div>
    <div class="election-results">
      {#each $selected.elections as election}
        <div class="result">
          <div class="party-name">
            <img
              class="party-icons"
              src="./assets/p-logo-opt/{election.Image}"
              alt="{election.PartyName} logo"
            />
          </div>
          <div class="party-result">
            {election.PercentOfVote.toFixed(0)}%
          </div>
          <div class="party-name-text">{election.PartyName}</div>
        </div>
      {/each}
    </div>
    <div class="index-icons">
      <div class="indicators">
        <h2>Aanwysers</h2>
        <div class="sub-head">
          Van die belangrikste faktore wat bydra tot die indeks
        </div>
        <MunicipalIndicators />

        <div class="disclaimer">
          <div>
            <img src="./assets/alert.svg" class="alert" alt="Warning" />
          </div>
          <div>
            Wees baie versigtig met die persentasies waar dit gaan oor
            dienslewering ­– veral in gevalle waar dit meer as 100% is. Die
            onwaarskynlike scenarios kan toegeskryf word aan die werklike getal
            huishoudings wat veel hoër is as die amptelike getal wat spruit uit
            die sensus-data van tien jaar gelede, of dat data oor dienslewering
            wat die munisipaliteite verskaf het, nie korrek is nie, terwyl die
            nodige gehalteversekering en toesig nie altyd gedoen word nie.
          </div>
        </div>
      </div>
    </div>
  </section>
</div>

<style>
  .party-result {
    font-size: 1.2rem;
    font-family: var(--bold-face);
    padding: 10px;
  }
  .election-results {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    padding: 20px;
    grid-gap: 20px;
    margin-bottom: 30px;
  }
  .section-title {
    margin-top: 30px;
  }

  .party-icons {
    max-height: 70px;
    max-width: 70px;
  }

  .party-name-text {
    font-size: 0.7rem;
    line-height: 120%;
    margin-top: 10px;
  }
  .party-result {
    background: #000;
    color: #fff;
    border-radius: 5px;
    padding: 3px;
    max-width: 50px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5px;
  }
  .municipal-selector {
    margin-top: 10px;
  }
  h2 {
    margin-bottom: 0px;
  }
  .sub-head {
    margin-bottom: 15px;
  }
</style>
