<script>
  import { selected, provincesMeta } from './stores/current-data.js'
  import { page } from './stores/page-state.js'
  import { onMount } from 'svelte'
  import { SimpleGauge } from './d3-simple-gauge.js'
  import { municipalData } from './stores/municipal-store.js'

  let gauges = []
  let topProvinces = []
  // let bottomProvinces = []
  import { elections, keyPoints } from './stores/key-points.js'
  import { indiMeta, indiData } from './stores/indicators.js'

  let ms = ['CPT', 'JHB', 'ETH', 'NMA']
  ms.forEach((m) => {
    let newM = $municipalData.filter((f) => f.code === m)
    topProvinces.push(newM[0])
  })

  // function getTopProvinces(selected) {
  //   return selected.provData
  //     .sort((a, b) => (a.total > b.total ? -1 : 1))
  //     .slice(0, 3)
  // }
  // function getBottomProvinces(selected) {
  //   return selected.provData
  //     .sort((a, b) => (a.total > b.total ? 1 : -1))
  //     .slice(0, 3)
  // }

  // $: topProvinces = getTopProvinces($selected)
  // $: bottomProvinces = getBottomProvinces($selected)

  function changeGauges() {
    if (gauges.length > 0) {
      gauges[1].value = topProvinces[0].total
      gauges[2].value = topProvinces[1].total
      gauges[3].value = topProvinces[2].total
      gauges[4].value = topProvinces[3].total
    }
  }

  // import { select } from 'd3-selection'
  // const d3 = { select }

  $: if ($selected.provCode) {
    changeGauges()
  }

  function addGauge(target, index) {
    const svg = d3
      .select('.' + target)
      .append('svg')
      .attr('class', 'svg')
      .attr('width', 150)
      .attr('height', 110)

    gauges[index] = new SimpleGauge({
      el: svg.append('g'), // The element that hosts the gauge
      height: 100, // The height of the gauge
      interval: [0, 100], // The interval (min and max values) of the gauge (optional)
      sectionsCount: 10, // The number of sections in the gauge
      width: 150, // The width of the gauge
      animationDuration: 1000,
      needleColor: '#000',
      chartInset: 0,
      easeType: d3.easeBounce,
      barWidth: 40,
      needleRadius: 7,
      sectionsColors: [
        '#A50728',
        '#E14B39',
        '#F78D59',
        '#FDC47C',
        '#FEEFAA',
        '#EBF6A9',
        '#C1E484',
        '#84CA6F',
        '#44AA5F',
        '#07693B',
      ],
    })
    gauges[index].value = 0
  }

  onMount(() => {
    addGauge('top-1', 1)
    addGauge('top-2', 2)
    addGauge('top-3', 3)
    addGauge('top-4', 4)
    // addGauge('bottom-1', 4)
    // addGauge('bottom-2', 5)
    // addGauge('bottom-3', 6)

    setTimeout(() => {
      changeGauges()
    }, 1000)
  })

  function showMunicipality(code) {
    $page.scrollMunicipality = true

    $page.home = false
    $page.province = false
    $page.municipal = true

    selected.update((val) => {
      val.provCode = $municipalData.filter((f) => f.code === code)[0].province

      val.muniCode = code
      val.muniData = $municipalData.filter((f) => f.code === code)[0]
      val.muniName = $municipalData.filter((f) => f.code === code)[0].name
      val.elections = $elections.filter((f) => f.Code === code)
      val.keyPoints = $keyPoints.filter((f) => f.code === code)[0]
      val.indData = $indiData.filter((f) => f.code === code)[0]
      val.indMeta = $indiMeta

      val.provName = $provincesMeta.filter(
        (f) => f.inCode === val.provCode
      )[0].name

      return val
    })
  }
</script>

<main>
  <div class="top-gauges">
    <div class="top-gauges-wrap">
      {#each topProvinces as top, i}
        <div class="top-gauge-wrap" on:click={() => showMunicipality(top.code)}>
          <div class="index-number">
            {top.total}<span class="hundred-indicator">/100</span>
          </div>
          <div class="gauge top-{i + 1}" />
          <div class="gauge-title">{top.name}</div>
        </div>
      {/each}
    </div>
  </div>
</main>

