  .index-number {
    font-family: var(--bold-face);
  }
  .hundred-indicator {
    color: lightgray;
  }
  .top-gauges {
    margin-bottom: 10px;
    /* background: rgb(46, 46, 46); */
    /* background: #fff; */
    /* background: #eee; */
    padding: 20px;
    padding-bottom: 5px;
    /* border: solid 1px lightgray; */
  }
  .top-gauges-wrap {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 10px;
  }
  .top-gauge-wrap {
    cursor: pointer;
  }
  .gauge {
    transform: translate(0px, -10px);
  }
  .top-title {
    color: #000;
    font-size: 1.2rem;
    margin-top: 0px;
    font-family: var(--bold-face);
    margin-bottom: 0px;
  }
  .title-best {
    color: green;
  }
  .title-worst {
    color: red;
  }
  .gauge-title {
    color: #000;
    font-size: 1rem;
    font-family: var(--bold-face);
    transform: translate(0px, -20px);
  }

  /* @media only screen and (max-width: 740px) {
    .top-gauges-wrap {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
    }
  } */

  @media only screen and (max-width: 740px) {
