  function getGaugeTotal(gv) {
    items = [
      'e',
      'f',
      'g',
      'h',
      'i',
      'j',
      'k',
      'l',
      'm',
      'n',
      'o',
      'p',
      'q',
      'r',
      's',
      't',
      'u',
      'v',
      'w',
      'x',
    ]

    let total = 0
    items.forEach((c) => {
      total = total + normalize(gaugeValues[c])
    })

    if (firstRun < 3) {
      total = indexTotal
      firstRun++
    }
    return total
  }

  function normalize(val) {
    let v = val
    if (v > 5) {
      v = 5
    }
    if (v < 0) {
      v = 0
    }
    return v
  }

