    }
  }

  // import { select } from 'd3-selection'

  // const d3 = { select }

  function addGauge(target, index) {
    const svg = d3
      .select('.' + target)
      .append('svg')
      .attr('class', 'svg')
      .attr('width', 300)
      .attr('height', 165)

    gauges[index] = new SimpleGauge({
      el: svg.append('g'), // The element that hosts the gauge
      height: 150, // The height of the gauge
      interval: [0, 100], // The interval (min and max values) of the gauge (optional)
      sectionsCount: 10, // The number of sections in the gauge
      width: 300, // The width of the gauge
      animationDuration: 1000,
      needleColor: '#000',
      chartInset: 10,
      easeType: d3.easeBounce,
      barWidth: 70,
      needleRadius: 10,
      sectionsColors: [
        '#A50728',
        '#E14B39',
        '#F78D59',
        '#FDC47C',
        '#FEEFAA',
        '#EBF6A9',
        '#C1E484',
        '#84CA6F',
        '#44AA5F',
        '#07693B',
      ],
    })
    gauges[index].value = 0
  }

  onMount(() => {
    addGauge('gauge-holder', 1)

    setTimeout(() => {
      changeGauge(+$selected.muniData.total)
    }, 700)
  })
  function lever1(value) {
    let affects = ['e', 'f', 'm']
    let val = +value.target.value
    let change = val - lever1Value
    lever1Value = val

    indexTotal = indexTotal + change * 2

    affects.forEach((e) => {
      gaugeValues[e] = gaugeValues[e] + change
    })
  }

